.portofolio {
	padding: 80px 0;
	@media  screen and (min-width: 992px) {
		padding: 150px 0;
	}
    position: relative;
	h1 {
		font-weight: 800;
	}
	.grid {
		&:last-child {
			margin-bottom: 0;
		}
		width: 100%;
		margin-bottom: 100px;
		display: grid;
		gap: 30px;
		grid-template-columns: repeat(12, 1fr);
		align-items: center;
		justify-content: center;
        position: relative;
		.image-wrapper {
			display: block;
			position: relative;
			grid-area: 1 / 1 / 1 / -1;
			@media screen and(min-width: 992px) {
				grid-area: 1 / 1/ 1 / 8;	
			}
			position: relative;
			box-shadow: 0 10px 30px -15px rgba(0, 0, 0, 0.5);
			top: 0px; left: 0px;
			transition: all .2s !important;
			transition-delay: .2s;
			@media screen and (min-width: 992px) {
                &:hover {
                    top: 3px; left: 3px;
                }
                &::before {
                    transition: .2s;
                    content: '';
                    position: absolute;
                    width: 100%; height: 100%;
                    border: 2px solid rgb(83, 83, 83);
                    top: 18px; left: 18px;
                }
                &:hover::before {
                    top: 12px; left: 12px;
                }
            }
		}
		.image {
			overflow: hidden;
			position: relative;
			&::before {
				content: '';
				width: 100%;
				height: 100%;
				position: absolute;
				background-color:rgba(53, 47, 140, 0.2);
				transition: .3s;
			}
			&:hover::before {
				opacity: 0;
			}
		}
		.content {
			text-align: right !important;
			position: relative;
			grid-area: 1 / 1 / 1 / -1;
			margin-top: 100px;
			@media screen and (min-width: 992px) {
				grid-area: 1 / 7 / 1 / -1;
				margin-top: 0;
			}
			
			h3 {
				font-weight: 800;
			}
			.white-box{
				box-shadow: 0 10px 30px -15px rgba(0, 0, 0, 0.4);
				background-color:white;
				padding: 24px;
				border-radius: 4px;
			}
			ul {
				list-style: none;
				padding: 0;
				margin: 0;
				@media screen and (min-width: 992px) {
					width: 80%

				}
				li {
					display: inline-block;
					margin-left: 28px;
					color: rgb(95, 95, 95);
				}
			}
			a {
				display: inline-block;
				overflow: auto;
				font-weight: 700;
			}	
		}
		&.left {
			.image-wrapper {
				@media screen and (min-width: 992px){
					grid-area: 1 / 6 / 1 / -1;
					&:hover {
						top: 3px; left: -3px;
					}
					&::before {
						top: 18px; left: -18px;
					}
					&:hover::before {
						top: 12px; left: -12px;
					}
				}
			}
			.content {
				text-align: left !important;
				@media screen and (min-width: 992px){
					grid-area: 1 / 1/ 1 / 7;
				}
				ul {
					justify-content: flex-start;
					li {
						margin-left: 0;
						margin-right: 28px;
					}
				}
			}
			a {
				div {
					span:nth-child(1) {
						transform: translateX(-100%);
					}
				}
				& > span {
					float: left;
				}
			}
		}
		@media screen and (max-width: 992px) {
			margin-bottom: 40px;
			align-items: unset ;
			.image-wrapper {
				box-shadow: none;
				.image {
					height: 200px !important;
					opacity: 0.3;
				}
				&::after {
					display: none;
				}
			}
			.content {
				padding: 20px;
				padding-bottom: 10px;
				.white-box {
					box-shadow: none;
					background-color: rgba(255, 255, 255, 0.7);
					border-radius: none;
				}
			}
		}
	}

	.design-portofolio {
		margin-top: 130px;
		@media screen and (max-width: 992px){
			margin-top: 70px;
		}
		h3 {
			font-weight: 800;
		}
		.portofolio-grid {
			display: block;
			.grid-item {
				display: block;
				margin-bottom: 30px;
				box-shadow: 0 20px 40px -15px rgba(0, 0, 0, 0.3);
				height: 200px;
				overflow: hidden;
				background-color:rgb(216, 199, 255);
				position: relative;
				.overlay {
					position: absolute;
					width: 100%; height: 100%;
					top: 50%; left: 50%;
					transform: translate(-50%, -50%);
					background-color: rgba(68, 0, 255, 0.5);
					opacity: 0;
					transition: .2s;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: 50px;
				}
				&:hover .overlay {
					width: calc(100% - 30px);
					height: calc(100% - 30px);
					opacity: 1;
				}
				& img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					object-position: top;
				}
			}
		}
		@media screen and (min-width: 720px){
			.portofolio-grid {
				display: grid;
				grid-template-columns: auto auto;
				grid-template-rows: minmax(300px, auto);
				grid-auto-flow: dense;
				gap: 30px;
				.grid-item {
					height: 300px;
					margin-bottom: 0px;
					&:nth-child(3) {
						grid-row-start: span 2;
						height: 630px;
					}
				}
			}
		}
		@media screen and (min-width: 992px) {
			.portofolio-grid {
				display: grid;
				grid-template-columns: auto auto auto;
				grid-template-rows: minmax(300px, auto);
				grid-auto-flow: dense;
				gap: 30px;
				.grid-item {
					height: 300px;
					margin-bottom: 0px;
					&:nth-child(1) {
						grid-column-start: span 2;
					}
					&:nth-child(4) {
						grid-column-start: span 2;
					}
					&:nth-child(3) {
						grid-row-start: span 2;
						height: 630px;
					}
					&:nth-child(5) {
						grid-column-start: span 2;
					}
				}
			}		
		}
	}
}