.about {
    padding: 80px 0;
	@media  screen and (min-width: 992px) {
		padding: 150px 0;
	}
    background-color: white;
	h1 {
		font-weight: 800;
	}
	.wrapper {
		position: relative;
		&::before {
			content: "";
			width: 100%; height: 100%;
			position: absolute;
			top: 30px; left: 30px;
			border: 2px solid #02233d;
			z-index: -1;
		}
		.img-wrapper {
			width: 300px;
			height: 350px;
			overflow: hidden;
			display: flex;
			align-items: center;
			img {
				width: 100%;
			}
		}
	}
	.buttons {
		@media screen and (max-width: 576px) {
			.btn {
				width: auto;
				font-size: 13px;
			}
		}
	}
}