.navbar {
	z-index: 1001;
	background-color: white;
	height: 80px;
	transition: all .8s ease;
	.navbar-brand {
		font-weight: 700;
		font-size: 24px;
	}
	.nav-item {
    	font-weight: 700;
		margin-left: 28px;
		.nav-link {
			transition: all .2s ease;
			position: relative;
			display: inline-block;
			width: 100%;
		}
	}
	.navbar-collapse {
		width: auto;
		.myresume {
			padding: 10px 20px;
			border: 1px solid white;
			background-color: #492f8c;
			border-radius: 5px;
			transition: all .2s ease;
			&:hover {
				color: #492f8c !important;
				background-color: white;
				border-color: #492f8c;
			}
		}
	}
	.navbar-toggler {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 60px;
		height: 50px;
	} 
	.sideNav {
		top: 0; left: 0;
		padding: 30px 20px;
		width: 65%;
		height: 100%;
		transition: .3s ease;
		ul li {
			padding: 10px;
			text-align: center;
			margin: 0;
			&:hover {
				background-color: white;
			}
		}
		.myresume {
			margin-top: 30px;
			display: block;
			text-align: center;
			padding: 10px 20px;
			border: 1px solid white;
			background-color: #492f8c;
			border-radius: 5px;
			transition: all .2s ease;
			&:hover {
				color: #492f8c !important;
				background-color: white;
				border-color: #492f8c;
			}
		}
		&.disabled {
			transform: translateX(-100%);
		}
		&.active {
			transform: translateX(0);
		}
	}
}