.get-in-touch {
	h1 {
		font-weight: 800;
	}
	.btn-wrapper {
		a {
			width: 300px;
			padding: 20px 30px;
			border: 2px solid #212529;
            color: #212529;
            text-decoration: none;
			transition: .2s;
			&:hover {
				background-color: #492f8c;
                border-color: #492f8c;
				color: white;
			}
		}
	}
}