.header {
	width: 100%;
	padding: 80px 0;
	.big-title {
		font-weight: 800;
		font-size: 70px;
		@media screen and (max-width: 450px){
			font-size: 50px;
		}
	}
    .bg-shape-purple {
        background-color: #492f8c;
        display: inline-block;
        color: white;
        padding: 10px;
        line-height: 0.7;
    }
	h5 {
        display: inline-block;
	}
    .line {
        display: inline-block;
        width: 170px;
        height: 10px;
        position: relative;
		&::after {
			@media screen and (max-width: 992px) {
				display: none;
			}
			content: '';
			position: absolute;
			top: 0; bottom: 0; right: 0;
			width: 150px;
			background-size: 130px 4px, 7px 7px;
			background-position: 0px 50%, 140px 50%;
			background-repeat: no-repeat;
			background-image: linear-gradient(90deg, #0a032b 0%, #0a032b 100%), radial-gradient(ellipse at center, #ffc514 0%, #ffc514 50%, rgba(255, 197, 20, 0) 50%, rgba(255, 197, 20, 0) 100%)
		}
    }
    img {
        @media screen and (max-width: 992px) {
			width: 90%;
			margin-top: 2rem;
		}
    }
}