.journey {
    padding: 80px 0;
	@media  screen and (min-width: 992px) {
		padding: 150px 0;
	}
    .big-title {
        font-weight: 800;
    }
    .education {
        .item {
            &:last-child {
                margin-bottom: 0px;
            }
            .left {
                width: 65px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                img {
                    width: 26px;
                }
                .line {
                    width: 26px;
                    height: calc(100% - 50px);
                    background-size: 3.5px 100%;
                    background-position: 50%;
                    background-repeat: no-repeat;
                    background-image: linear-gradient(90deg, #2f2f2f 0%, #2f2f2f 100%)
                }
            }
            margin-bottom: 2.5rem;
            h5 {
                margin-bottom: 1rem;
            }
            h3 {
                font-weight: 800;
                letter-spacing: 0.1rem;
                margin-bottom: 1rem;
                line-height: 1.4em;
            }
            ul {
                text-decoration: none;
                list-style: none;
                padding: 0;
                margin: 0;
                li {
                    margin-bottom: 4px;
                }
            }
        }
    }
    .skills {
        .title {
            font-weight: 800;
        }
        ul {
            text-decoration: none;
            list-style: none;
            padding: 0;
            margin: 0;
            li {
                .line {
                    margin: 0 20px;
                    flex: 1;
                    background-size: 100% 1px;
                    background-position: 50%;
                    background-repeat: no-repeat;
                    background-image: linear-gradient(#a0a0a0 0%, #a0a0a0 100%)
                }
                span {
                    &:last-child {
                        width: 100px;
                    }
                    &:first-child {
                        width: 140px;
                    }
                }

                display: flex;
                justify-content: space-between;
                margin-bottom: 4px;
            }
        }
    }
}